.item{
    padding: 10px 20px;
    margin-bottom: 1rem;
    border: 1px solid #333;
    display: flex;
    justify-content: space-between;
}
.info{
    display: flex;
    justify-content: space-between;
    flex: 0 0 55%;
    line-height: 3rem;
}
.expense{
    margin-right: 2rem;
}
.amount{
    padding: 3px;
}
.edit-btn, .clear-btn{
    border-radius: 10%;
}
.edit-btn{
    color: var(--mainGreen);
}
.edit-btn:hover{
    color: var(--mainWhite);
    background-color: var(--mainGreen);
}
.clear-btn{
    color: var(--mainRed);
}
.clear-btn:hover{
    color: var(--mainWhite);
    background-color: var(--mainRed);
}